import React, { Component } from 'react';
import ButtonAnimation from '../common/ButtonAnimation';
import classNames from 'classnames';
import Dropzone from 'react-dropzone'

class Vacancy extends Component {

	constructor(props) {
		super(props);

		this.profession = null;

		this.professions = [{
			title: 'QA Engineer',
			price: '100 000  120 000 .'
		}, {
			title: 'Support Engineer',
			price: '100 000  120 000 .'
		}, {
			title: 'Lead Web-',
			price: '100 000  120 000 .'
		}, {
			title: ' Front-End ',
			price: '100 000  120 000 .'
		}, {
			title: ' Web-',
			price: '100 000  120 000 .'
		}, {
			title: 'HTML-',
			price: '100 000  120 000 .'
		}];

		this.professionSkills = [{
			title: '  QA Engineer',
			firstSkill: '  QA Engineer',
			secondSkill: '  QA Engineer',
			thirdSkill: '   QA Engineer',
			fourthSkill: '    (   ) QA Engineer'
		}, {
			title: '  Support Engineer',
			firstSkill: '  Support Engineer',
			secondSkill: '  Support Engineer',
			thirdSkill: '   Support Engineer',
			fourthSkill: '    (   ) Support Engineer'
		}, {
			title: '  Lead Web-',
			firstSkill: '  Lead Web-',
			secondSkill: '  Lead Web-',
			thirdSkill: '   Lead Web-',
			fourthSkill: '    (   ) Lead Web-'
		}, {
			title: '   Front-End ',
			firstSkill: '   Front-End ',
			secondSkill: '   Front-End ',
			thirdSkill: '    Front-End ',
			fourthSkill: '    (   )  Front-End '
		}, {
			title: '   Web-',
			firstSkill: '   Web-',
			secondSkill: '   Web-',
			thirdSkill: '    Web-',
			fourthSkill: '    (   )  Web-'
		}, {
			title: '  HTML-',
			firstSkill: '  HTML-',
			secondSkill: '  HTML-',
			thirdSkill: '   HTML-',
			fourthSkill: '    (   ) HTML-'
		}];

		this.professionOffers = [{
				title: ' ',
				firstOffer: '  QA Engineer',
				secondOffer: '  QA Engineer',
				thirdOffer: '  ',
				fourthOffer: '- '
			}, {
				title: ' ',
				firstOffer: '  Support Engineer',
				secondOffer: '  Support Engineer',
				thirdOffer: '  ',
				fourthOffer: '- '
			}, {
				title: ' ',
				firstOffer: '  Lead Web-',
				secondOffer: '  Lead Web-',
				thirdOffer: '  ',
				fourthOffer: '- '
			}, {
				title: ' ',
				firstOffer: '  Front-End ',
				secondOffer: '   Front-End ',
				thirdOffer: '  ',
				fourthOffer: '- '
			}, {
				title: ' ',
				firstOffer: '   Web-',
				secondOffer: '   Web-',
				thirdOffer: '  ',
				fourthOffer: '- '
			}, {
				title: ' ',
				firstOffer: '  HTML-',
				secondOffer: '  HTML-',
				thirdOffer: '  ',
				fourthOffer: '- '
		}];

		this.state = {
			activeProfession: 0,
			activeSkillOffers: true,
			activeSkill: 0,
			activeOffer: 0,
			activeForm: false,
			buttonText: '',
			files: []
		};
	}

	handleChange = (event, profession) => {
		event.stopPropagation();
		this.setState({
			activeProfession: profession,
			activeSkillOffers: true,
			activeSkill: profession,
			activeOffer: profession,
			activeForm: false,
			buttonText: ''
		})
	};

	showForm = (event, profession) => {
		event.stopPropagation();

		this.state.activeForm ? (
			this.setState({
				activeProfession: profession,
				activeSkillOffers: true,
				activeSkill: profession,
				activeOffer: profession,
				activeForm: false,
				buttonText: ''
			})
		) : (
		this.setState({
			activeProfession: profession,
			activeSkillOffers: false,
			activeSkill: null,
			activeOffer: null,
			activeForm: true,
			buttonText: '  '
		}) )
	};

	onDrop(files) {
		this.setState({
			files
		});
	}

	render() {

		const formClasses = classNames('col-md-4 col-sm-4 col-md-offset-1 col-sm-offset-1 respond', { 'active': this.state.activeForm });
		const skillOffersClasses = classNames('col-md-4 col-sm-4 col-md-offset-1 col-sm-offset-1 skills-offers', { 'active': this.state.activeSkillOffers });
		const buttonClasses = classNames('button', { 'active': this.state.activeForm });

		return (

			<div className="third-screen">

				<div className="header">

					<div className="gutter-10 row">

						<div className="col-md-3 col-md-offset-2 col-sm-3 col-sm-offset-2 column">

							{ this.professions.map( (profession, index) => {

								const classes = classNames('profession', { 'active': index === this.state.activeProfession });

								return(

									<div
										key={`profession-${index}`}
										className={classes}
										onClick={(event)=>this.handleChange(event, index)}
									>
										<h2>{profession.title}</h2>
										<div className="price">{profession.price}</div>
										<ButtonAnimation />
										<div
											className={buttonClasses}
											onClick={(event)=>this.showForm(event, index)}
										>{this.state.buttonText}</div>
									</div>

								)}
							)}

						</div>

						<div className={skillOffersClasses}>

							{ this.professionSkills.map((profession, index)=>{

								const classes = classNames('skills', { 'active': index === this.state.activeSkill });

								return(
									<div
										key={`skills-${index}`}
										className={classes}
									>
										<div className="title">{profession.title}</div>
										<p>{profession.firstSkill}</p>
										<p>{profession.secondSkill}</p>
										<p>{profession.thirdSkill}</p>
										<p>{profession.fourthSkill}</p>
									</div>
								)}
							)}

							{
								this.professionOffers.map( (profession, index) => {

									const classes = classNames('offer', { 'active': index === this.state.activeOffer });

									return(
										<div
											key={`offer-${index}`}
											className={classes}
										>
											<div className="title">{profession.title}</div>
											<p>{profession.firstOffer}</p>
											<p>{profession.secondOffer}</p>
											<p>{profession.thirdOffer}</p>
											<p>{profession.fourthOffer}</p>
										</div>
									)
								})
							}

						</div>

						<form action="#" name="send-respond" className={formClasses}>

							<div className="wrap">

								<input type="text" required placeholder="" id="name-respond"/>
								<label htmlFor="name-respond"></label>

								<input name="email" type="email" required placeholder="Email" id="email-respond"/>
								<label	htmlFor="email-respond"></label>

								<input type="text" placeholder="" id="phone-respond"/>
								<label htmlFor="phone-respond"></label>

								<input type="text" placeholder=" " id="pay-respond"/>
								<label htmlFor="pay-respond"></label>

								<Dropzone onDrop={this.onDrop.bind(this)} className="drop-zone" >
									<p> </p>
								</Dropzone>

								<aside className="drop-file">
									<ul>
										{
											this.state.files.map((f, index) => <li key={`file-${index}`}>{f.name} - {f.size} bytes</li>)
										}
									</ul>
								</aside>

							</div>

							<ButtonAnimation />

							<button type="submit" id="show-form-button"> </button>

						</form>

					</div>

				</div>

			</div>

		)
	}
}

export default Vacancy;



